@import "../../../public/sass/util";

.border-box {
	box-sizing: border-box;

	* {
		box-sizing: border-box;
	}
}

.wpr-flex {
	display: flex;
	flex-wrap: wrap;
}

.wpr-flex-wrap {
	flex-wrap: wrap;
}

.wpr-col-1-2 {
	flex: 0 0 50%;
}

.wpr-col-1-3 {
	flex: 0 0 33.33%;
	margin-bottom: 12px;
	padding-bottom: 12px;

	label {
		position: relative;
		padding-left: 20px;
		input[type=checkbox] {
			position: absolute;
			top: 0;
			left: 0;
			-webkit-box-shadow: none;
			box-shadow: none;
			overflow: hidden;
			border: 2px solid rgba(33,38,44,0.6);
			border-radius: 2px;

			&:checked {
				border-color: #069de3;
				&:before {
					line-height: 16px;
					width: 17px;
					height: 16px;
					margin: -2px 0 0 -5px;
					content: '\f147';
					text-align: center;
					color: #fff;
					border-radius: 2px;
					background: #069de3;
				}
			}
		}
	}
}

.wp-review {

	* {
		box-sizing: border-box;
	}

	p.submit { text-align: right; }

	h1 { margin-bottom: 15px; }
}
.wpr-flex-wrap { justify-content: space-between; }
.wpr-flex-wrap .wpr-col-1-2 {
	flex: 0 0 49%;
}
.wpr-flex-wrap .wpr-col-1-2:last-child { float: right; }
/*---------------------------
 *          Fields
 *--------------------------*/
.wp-review-field {
	&, * {
		box-sizing: border-box;
	}

	padding: 15px 0;
	border-bottom: 1px dashed #ddd;
	margin-top: 1em;
	margin-bottom: 1em;
	border-bottom: 1px dashed #ddd;
}

.postbox .wp-review-field {
	padding: 0;
	border-bottom: 0;
}

@media (min-width: 600px) {
	.wp-review-field:not(.vertical):not(.no-flex) {
		display: flex;
		flex-wrap: wrap;

		.wp-review-field-label {
			flex: 0 0 25%;
			padding-right: 4%;

			&.full-width {
				flex: 0 0 100%;
				padding-right: 0;
			}
		}

		.wp-review-field-option {
			flex: 0 0 75%;
		}

		&.wp-review-rating-icon, &.wp-review-rating-image {
			position: relative;
			&.hide {display: none;}
			a.wpr-toggle-rating {
				position: absolute;
				top: 0;
				right: 0;
			}

			.img-wrapper {
				padding: 2px;
				width: 55px;
				height: 55px;
				border: 1px solid #ddd;
				position: relative;
				img {
					max-width: 100%;
				}
				i {
					position: absolute;
					top: -6px;
					right: -6px;
					color: #444;
					cursor: pointer;
				}
				&.hide { display: none; }
			}
			.set_rating_image.hide {display: none;}
		}
	}

	.postbox .wp-review-field {
		.wp-review-field-label {
			flex: 0 0 20%;
		}

		.wp-review-field-option {
			flex: 0 0 80%;
		}
	}
}

.wpr_image_upload_field {
	.clear-image {
		margin-left: 10px;
	}
}

