/*---------------------------
 *       Options page
 *--------------------------*/
.wpr-vertical-tabs {
	max-width: 880px;
	position: relative;

	&__titles {
		width: 200px;
		float: left;
		border-right: 0;
		margin: 0;
		z-index: 10;
		position: absolute;
		left: 0;
		top: 0;

		&.active {
			background-color: #fff;
		}
	}

	&__title {
		margin: 0;
		padding: 0;
		position: relative;

		&:first-child a { border-top: 1px solid #ccc; }

		&.active a {
			background-color: #fff;
		}

		&.active a:after {
			content: "";
			width: 1px;
			height: 40px;
			position: absolute;
			right: -1px;
			top: 1px;
			background: #fff;
			z-index: 10;
		}

		i {
			font-size: 18px;
			min-width: 20px;
			margin-right: 10px;
			text-align: center;
			color: #444;
		}

		a {
			font-size: 16px;
			display: block;
			-webkit-box-sizing: border-box;
			box-sizing: border-box;
			padding: 11px 15px;
			white-space: nowrap;
			text-decoration: none;
			color: #555;
			border: 1px solid #ccc;
			border-top: 0;
			border-right: 0;
			outline: none;
			background: #e5e5e5;
			-webkit-box-shadow: none;
			box-shadow: none;
		}
	}

	&__contents {
		padding-left: 200px;
	}

	&__content {
		padding: 15px;
		min-height: 400px;
		background: #fff;
		border: 1px solid #ccc;

		> h2 { margin-top: 0; }

		.nav-tab-active {
			background-color: #fff;
			border-bottom-color: #fff;
		}
	}
}

