// Form fields
.wpr-switch {
	$border-radius: 3px; // Default 4px
	$font-size: 12px; // Default 14px
	$button-width: 45px; // Default 50px
	$height: 28px;

	display: inline-block;

	input {
		position: absolute !important;
		clip: rect(0, 0, 0, 0);
		height: 1px;
		width: 1px;
		border: 0;
		overflow: hidden;
	}

	label {
		float: left;
		display: inline-block;
		min-width: $button-width;
		background-color: #e4e4e4;
		color: rgba(0, 0, 0, 0.6);
		font-size: $font-size;
		font-weight: 700;
		line-height: $height;
		text-align: center;
		text-shadow: none;
		border: 1px solid rgba(0, 0, 0, 0.2);
		box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3), 0 1px rgba(255, 255, 255, 0.1);
		transition: all 0.1s ease-in-out;

		&:hover {
			cursor: pointer;
		}

		&:first-of-type {
			border-radius: $border-radius 0 0 $border-radius;
			border-right: 0;
			font-weight: normal;
		}

		&:last-of-type {
			border-radius: 0 $border-radius $border-radius 0;
			border-left: 0;
			font-weight: normal;
		}
	}
}

input.wpr-switch__on:checked + label, input.wpr-switch__off:checked + label {
	background: #0085ba;
	border-color: #0073aa #006799 #006799;
	box-shadow: 0 1px 0 #006799;
	color: #fff;
	text-decoration: none;
	text-shadow: 0 -1px 1px #006799, 1px 0 1px #006799, 0 1px 1px #006799, -1px 0 1px #006799;
	margin-bottom: 2px;
}
input.wpr-switch__on:checked + label:hover, input.wpr-switch__off:checked + label:hover {
	background: #008ec2;
	border-color: #006799;
	color: #fff;
}
input.wpr-switch__off:checked + label, input.wpr-switch__off:checked + label:hover {
	color: #fff;
	background: #f5786f;
	border-color: #bd4c44 #bd4c44 #bd4c44;
	-webkit-box-shadow: 0 1px 0 #bd4c44;
	box-shadow: 0 1px 0 #bd4c44;
	text-shadow: none;
}

